input[type='radio'] {
    -webkit-appearance: none;
    appearance: none;

    border-radius: 50%;
    width: 18px;
    height: 18px;

    border: 1px solid black;
    transition: 0.2s all linear;
    margin-right: 5px;

    position: relative;
    top: 1px;
    }

    input[type='radio'] {
    -webkit-appearance: none;
    appearance: none;  
    width: 18px;
    height: 18px;
    border: 1px solid white;
    transition: 0.2s all linear;
    margin-right: 5px;
    position: relative;
    top: 1px;
    }

    input[type='radio']:checked, input[type='radio']:checked {
      border: 1px solid black;
      background: #FDCC12;
    }

.filtro{
